<template>
	<div>
		<ContentHeader3 title="Administrasi" url="/administrasi" title2="Data Jasa " url2="/data-jasa-pemeriksaan" subTitle="Tambah Data Jasa" />
		<div class="content-header">
			<div class="container-fluid">
				<div class="row justify-content-center">
		            <div class="col">
		            	<div class="card">
		            		<div class="card-header row">
		            			<h3 class="card-title">Input Data Administrasi</h3>
		            		</div>
		            		<div class="card-body">
		            			<form @submit.prevent="prosesDataAdministrasi">
					                <div class="px-lg-2 ">
					                  <div class="form-group">
					                    <label for="kode_administrasi">Kode Administrasi</label>
					                    <input type="text" class="form-control" id="kode_administrasi" name="kode_administrasi"  disabled="" v-model="administrasi.kode_administrasi">
					                  </div>
					                  <div class="form-group">
					                    <label for="nama">Nama <span class="text-danger">*</span></label>
					                    <input type="text" class="form-control" id="nama" name="nama" autocomplete="off" placeholder="Nama Administrasi" v-model="administrasi.nama" required="">
					                  </div>
					                  <div class="form-group">
					                    <label for="fee_dokter">Tarif <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Rp.</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="harga" name="harga" placeholder="Fee Dokter" v-model="administrasi.harga" required="">
						                </div>
					                  </div>
					                  <div class="form-group">
					                    <label for="fee_dokter">Fee Dokter <span class="text-danger">*</span></label>
					                    <div class="input-group mb-3">
						                  <div class="input-group-prepend">
						                    <div type="button" class="btn btn-secondary">Rp.</div>
						                  </div>
						                  <!-- /btn-group -->
					                      <input type="number" class="form-control" id="fee_dokter" name="fee_dokter" placeholder="Fee Dokter" v-model="administrasi.fee_dokter" required="">
						                </div>
					                  </div>
					                  <button type="submit" class="btn btn-primary mt-3">Submit</button>
					                </div>
					                <!-- /.card-body -->					               
					              </form>
		            		</div>
		            	</div>
		            </div>
		        </div>
			</div>
		</div>
	</div>
</template>

<script>
import ContentHeader3 from '@/components/ContentHeader3'

import { reactive, computed, onMounted } from 'vue'
import store from '@/store'
import axios from 'axios'
import tambah_nol from '@/tambah_nol.js'
import Swal from 'sweetalert2'
import router from '@/router'

export default{
	components: {
		ContentHeader3: ContentHeader3
	},
	setup(){
		const user = computed(() => store.getters['auth/user'])

	    // Generate Kode Tindakan
		const kode_administrasi_baru = reactive({})
	    const generateKodeTindakan = () => {
	    	axios.get(`api/cabang/getWhereId/${user.value.cabang_id}`)
	    	.then((response) => {
	    		kode_administrasi_baru.value = `${response.data.kode_cabang}-J`
	    	}) 
	    	.catch((error) => {
	    		console.log(error)
	    	})

	    	axios.get(`api/administrasi/getLastIdJasa/${user.value.cabang_id}`)
	    	.then((response) => {
	    		console.log(response)
	    		if(response.data.id == null){
	    			kode_administrasi_baru.value = `${kode_administrasi_baru.value}-${tambah_nol(1)}`
	    		} else {
	    			kode_administrasi_baru.value = `${kode_administrasi_baru.value}-${tambah_nol(response.data.id + 1)}`
	    		}
	    	})
	    	.catch((error) => {
	    		console.log(error)
	    	})
	    }
	    const kode_baru = computed(() => kode_administrasi_baru.value)
	    // End

		const administrasi = reactive({
			cabang_id: user.value.cabang_id,
			kode_administrasi: kode_baru,
			jenis_administrasi: 'jasa',
			nama: '',
			harga: 0,
			fee_dokter: 0
		})

		// Proses Store Data
	    const prosesDataAdministrasi = () => {
			axios.post('api/administrasi/create', administrasi)
			.then((response) => {
				Swal.fire({
				  title: 'Berhasil!',
				  text: 'Berhasil menambahkan data',
				  icon: 'success',
				  confirmButtonText: 'Ok'
				})	
				console.log(response)
			})
			.catch((error) => {
				Swal.fire({
				  title: 'Gagal!',
				  text: 'Gagal menambahkan data',
				  icon: 'error',
				  confirmButtonText: 'Ok'
				})	
				console.log(error)
			})

			router.replace('/data-jasa-pemeriksaan')
	    }
	    // End

		onMounted(() => {
	    	generateKodeTindakan()
	    })

		return { administrasi, kode_baru, prosesDataAdministrasi }

	}
}
</script>